.image-wrapper {
    background-color: var(--color-black);
}

.bg-image {
    width: 100%;
    height: 100vh;
    background: url('../../assets/bg3.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    opacity: 0.4;
}
