
.app__footbar {
    position: fixed;
    bottom: 5rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.app__footbar-menuToggle {
    position: relative;
    width: 65px;
    height: 65px;
    /* background-color: var(--color-white); */
    background-color: #ff4886;
    /* border: 5px solid transparent; */
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    transform: translateY(-30px);
}
.app__footbar-menuToggle::before {
    content: '+';
    position: absolute;
    font-size: 3em;
    color: #fff;
    transition: 1.5s;
}
/* .app__footbar-menuToggle:hover::before {
    transform: rotate(225deg)
} */
.app__footbar-menuToggle.active::before {
    transform: rotate(225deg)
}


.app__footbar-menu {
    position: absolute;
    width: 520px;
    height: 68px;
    background: var(--color-white);
    border-radius: 100px;
    padding: 1rem 1rem;
}

.app__footbar-menu ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.app__footbar-menu ul li {
    list-style: none;
    font-family: 'Chillax', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: var(--color-black);
    padding: 0.75rem 1rem;
    transition: .7s ease-out;
}

.app__footbar-menu ul li.active {
    background-color: #ededed;
    border-radius: 100px;
}

.app__footbar-menu ul li.btn {
    flex: 1;
    background-color: var(--color-black);
    border-radius: 100px;
    cursor: pointer;
    position: relative;
}
.app__footbar-menu ul li button {
    background: none;
    color: var(--color-white);
    padding: 0.1rem 0.5rem;
    letter-spacing: .2em;
    border: none;
}
.app__footbar-menu ul li.btn a::before {
    position: absolute;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    top: 0;
    left: 0;
}
.app__footbar-menu ul li.btn a::before:hover {
    cursor: pointer;
}



@media screen and (max-width: 650px) {
    .app__footbar-menu {
        width: 400px;
    }
    .app__footbar-menu ul li {
        font-size: 12px;
    }
    .app__footbar-menu ul li button {
        font-size: 12px;
        padding: 0 0;
    }
}

@media screen and (max-width: 450px) {
    .app__footbar-menu {
        width: 350px;
    }
    .app__footbar-menu ul li {
        font-size: 10px;
    }
    .app__footbar-menu ul li button {
        font-size: 8px;
        line-height: 12px;
    }
}