.app__menu .app__menu-right {
    width: 100%;
    height: 100vh;
    /* display: flex; */
    flex-direction: column;
    justify-content: flex-start;
    /* align-items: center; */
    position: relative;
}

.app__menu .app__menu-right .scrollable-div {
    overflow-y: scroll;
    /* width: 100%; */
    /* display: flex; */
    /* justify-content: flex-start; */
    /* align-items: center; */
    /* flex-direction: column; */
    scroll-behavior: smooth;
}
.app__menu .app__menu-right .scrollable-div::-webkit-scrollbar {
    display: none;
}

.app__menu .app__menu-right nav {
    margin-bottom: 0rem;
    position: sticky;
    top: 0;
}

.app__menu .app__menu-right nav ul {
    /* display: flex;
    justify-content: center;
    align-items: center; */
}
.app__menu .app__menu-right nav ul li {
    font-family: 'Chillax', sans-serif;
    color: var(--color-white);
    list-style: none;
    line-height: 25.6px;
    padding: 1rem;
}
.app__menu .app__menu-right nav ul li.active {
    color: var(--color-primary);
}

.app__menu .app__menu-right nav ul li a {
    cursor: pointer;
}


