.app__header-overlay {
    /* background-color: var(--color-black); */
    background-color: #050505;
}


.app__header {
    background: url('../../assets/cover\ photo2.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
}

.app__header h3 {
    font-family: 'Bitter Rose', sans-serif;
    color: #FACE8D;
    font-size: 70px;
    font-weight: 400;
    line-height: 40px;
}

.app__header h1 {
    font-family: 'Chillax', sans-serif;
    color: #fff;
    font-size: 140px;
    font-weight: 500;
    line-height: 144px;
    cursor: pointer;
}
.app__header p {
    font-family: 'Chillax', sans-serif;
    color: #fff;
    font-size: 21px;
    font-weight: 400;
    line-height: 38.4px;
    width: 40%;
    text-align: center;
}

.app__header-info {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.app__header-time {
    position: absolute;
    left: 42%;
    top: 15%;
    background-color: var(--color-white);
    width: 250px;
    padding: 6px 24px;
    border-radius: 10px;
    z-index: 1;
}
.app__header-time p {
    color: var(--color-black);
    width: 100%;
}

.app__header-time .head-item p {
    text-align: left;
    font-size: 14px;
}
.app__header-time .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.app__header-time .item p {
    font-size: 12.4px;
}


@media screen and (max-width: 1150px) {
    .app__wrapper {
        flex-direction: row !important;
    }

    .app__header p {
        width: 60%;
    }
}

@media screen and (max-width: 850px) {
    .app__header p {
        width: 70%;
    }
}

@media screen and (max-width: 650px) {
    .app__header h3 {
        font-size: 50px;
    }
    .app__header h1 {
        font-size: 100px;
    }
    .app__header p {
        width: 80%;
    }
}

@media screen and (max-width: 450px) {
    .app__header h3 {
        font-size: 40px;
    }
    .app__header h1 {
        font-size: 80px;
    }
    .app__header p {
        width: 100%;
    }
}