.section__padding {
  padding: 4rem 6rem;
}

.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__bg {
  background: url('./assets/bg.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
  background-attachment: fixed;
}

.app__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
}

.app__wrapper_info,
.app__wrapper_img {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.app__wrapper_img {
  align-items: center;
  flex-direction: row;
  margin-left: 2rem;
}

.app__wrapper_img-reverse {
  justify-content: flex-start;
  margin-right: 2rem;
}

.app__wrapper_img img {
  width: 80%;
}

.custom__button {
  border: 2px solid transparent;
  border-radius: 3rem;
  color: var(--color-black);
  background-color: var(--color-primary);
  font-family: 'Chillax', sans-serif;
  font-size: 16px;
  font-weight: 500;
  overflow: hidden;
  padding: 1rem 2rem;
  position: relative;
  transition: .3s transform ease-in-out;
  will-change: transform;
  z-index: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.custom__button::after {
  background-color: var(--color-black);
  border-radius: 3rem;
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transform: translate(-100%, 0) rotate(10deg);
  transform-origin: top left;
  transition: .3s transform ease-in-out;
  will-change: transform;
  z-index: -1;
}
.custom__button:hover::after {
  transform: translate(0, 0);
}
.custom__button:hover {
  cursor: pointer;
  border: 2px solid var(--color-primary);
  color: var(--color-primary);
  transform: scale(1.05);
  will-change: transform;
}


.p__prim {
  /* font-family: var(--font-base); */
  font-family: 'Cormorant Upright', sans-serif;
  color: var(--color-white);
  font-weight: 700;
  letter-spacing: 0.04em;
  font-feature-settings: 'tnum' on, 'lnum' on;
  line-height: 29.9px;
  font-size: 23px;
}

.p__sec {
  /* font-family: var(--font-alt); */
  font-family: var(--font-primary);
  color: var(--color-white);
  font-weight: 400;
  letter-spacing: 0.04em;
  line-height: 28px;
  font-size: 20px;
}

.head__text {
  font-family: var(--font-base);
  color: var(--color-golden);
  font-size: 64px;
  line-height: 83.2px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
}

.fork__img {
  width: 45px;
}

@media screen and (min-width: 2000px) {

  .p__prim {
    font-size: 37px;
    line-height: 67px;
  }
  .p__sec {
    font-size: 30px;
    line-height: 50px;
  }
  .head__text {
    font-size: 150px;
    line-height: 210px;
  }
  .fork__img {
    width: 80px;
  }
}

@media screen and (max-width: 1150px) {
  .app__wrapper {
    flex-direction: column;
  }
  .app__wrapper_img {
    margin: 5rem 0 0 0;
  }
  .app__wrapper_img-reverse {
    margin: 0 0 5rem 0;
  }
  .app__wrapper_img img {
    width: 100%;
  }
}

@media screen and (max-width: 850px) {
  .section__padding {
    padding: 4rem;
  }
}

@media screen and (max-width: 650px) {
  .section__padding {
    padding: 4rem 2rem;
  }
  .p__prim {
    font-size: 21px;
  }
  .p__sec {
    font-size: 14px;
  }
}

@media screen and (max-width: 450px) {
  .p__prim {
    font-size: 19px;
  }
  .p__sec {
    font-size: 12px;
  }
  .head__text {
    font-size: 45px;
    line-height: 70px;
  }
}