.app__contact .app__contact-right {
    width: 100%;
    flex-direction: column;
}
.app__contact .app__contact-right_p {
    width: 100%;
    display: flex;
    justify-content: center;
}

.app__contact .app__contact-right p {
    padding: 1rem 1rem;
    font-size: 20px;
    line-height: 32px;
    width: 680px;
    opacity: .8;
    text-align: justify;
}

.app__contact .app__contact-right form {
    font-size: 16px;
    line-height: 25.6px;
    width: 100%;
}

.app__contact .app__contact-right form > div {
    display: flex;
    flex-direction: column;
    /* justify-content: center;     */
    /* align-items: center; */
    margin: 2rem 4rem;

}

.app__contact .app__contact-right form label {
    opacity: .8;
}
.app__contact .app__contact-right form input,
.app__contact .app__contact-right form textarea {
    color: var(--color-white);
    padding: .75rem 1.5rem;
    border-radius: 10px;
    margin: 1rem 0;
    background-color: rgba(0, 0, 0, 0);
    /* width: 100%; */
    border: 1px solid rgba(255, 255, 255, 0.1)
}

.app__contact .app__contact-right form input::placeholder,
.app__contact .app__contact-right form textarea::placeholder {
    opacity: .8;
    color: var(--color-white);
    font-family: 'Chillax', sans-serif;
}

.app__contact .app__contact-right form .custom__button {
    /* color: var(--color-black); */
    /* font-family: 'Chillax', sans-serif; */
    /* font-weight: 500; */
    /* letter-spacing: 1px; */
}


@media screen and (min-width: 2000px) {

    .app__contact .app__contact-right p {
        font-size: 22.5px;
    }
    

    .app__contact .app__contact-right form,
    .app__contact .app__contact-right form input,
    .app__contact .app__contact-right form textarea {
        font-size: 18px;
    }
}

@media screen and (max-width: 1560px) {
    .app__contact .app__contact-right_p {
        width: 80%;
    }
}

@media screen and (max-width: 1200px) {
    .app__contact {
        flex-direction: column;
        height: 200vh;
    }

}


@media screen and (max-width: 650px) {
    /* .app__contact .app__contact-left h3 {
        font-size: 30px;
    }
    .app__contact .app__contact-left h2 {
        font-size: 75px;
    }
    
    .app__contact .app__contact-left h1 {
        font-size: 60px;
    } */
    .app__contact .app__contact-right p {
        font-size: 16px;
    }
    .app__contact .app__contact-right form,
    .app__contact .app__contact-right form input,
    .app__contact .app__contact-right form textarea {
        font-size: 13px;
    }

}

@media screen and (max-width: 450px) {
    /* .app__contact .app__contact-left h1 {
        font-size: 50px;
    } */

    .app__contact .app__contact-right p {
        font-size: 13px;
    }
    .app__contact .app__contact-right form,
    .app__contact .app__contact-right form input,
    .app__contact .app__contact-right form textarea {
        font-size: 11px;
    }
}