.app__footer {
    background-color: var(--color-black);
    color: var(--color-white);
    font-family: var(--font-primary);
    display: flex;
    flex-direction: column;
}

.app__footer p,
.app__footer li {
    color: #757575;
    list-style: none;
}
.app__footer li:hover {
    color: indianred;
}


.app__footer-about {
    padding: 3rem 10rem;
    width: 100%;
    border-bottom: 1px solid #373636;
    justify-content: space-between !important;
}

.w__container {
    cursor: pointer;
}

.w__container svg {
    color: var(--color-primary);
    margin-right: 15px;
    font-size: 40px
}

.w__container h4 {
    font-size: 20px;
    font-weight: 600;
}


.app__footer-links {
    display: flex;
    justify-content: space-between !important;
    margin-top: 2rem;
}

.app__footer-links_social {
    display: flex;
    flex-direction: column;
}
.app__footer-links_social p {
    width: 400px;
    margin: 1.5rem 0;
}

.app__footer .social__icons > div {
    width: 40px;
    height: 40px;
    margin: 1rem 1.25rem 0 0;
    border-radius: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__footer .social__icons > div svg {
    font-size: 20px;
}

.app__footer-links_links {
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: flex-start !important;
    /* width: 100%; */
    padding: 0 2rem;
}
.app__footer-links_links h3 {
    /* width: 100%; */
    /* text-align: left; */
    /* align-self: flex-start; */
    /* margin-left: 22%; */
}
.app__footer-links_links > div {
    display: flex;
}

.app__footer-links_links ul {
    margin-right: 3rem;
}
.app__footer-links_links ul li {
    margin: 1rem 0;
    font-size: 17px;
}

.app__footer-links h3 {
    margin-bottom: 1.5rem;
    font-size: 20px;
    font-weight: 600;
    padding: 0.5rem 0;
    position: relative;
}
.app__footer-links h3::after {
    content: '';
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: var(--color-golden);
    left: 0;
    bottom: 0;
}

.app__footer-links_form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* width: 100%; */
}
.app__footer-links_form p {
    width: 400px;
}

.app__footer-links_form input {
    background-color: #2E2E2E;
    padding: 14px 28px;
    font-size: 15px;
    font-family: var(--font-primary);
    width: 100%;
    border: none;
    color: var(--color-white);
}

.app__footer-links_form button {
    position: absolute;
    top: 0;
    right: 0;
    padding: 12px 16px;
    background-color: var(--color-primary);
    font-size: 20px;
    cursor: pointer;
}

.app__footer-copyright {
    background-color: #202020;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 2rem 6rem;
}

.app__footer-copyright ul li {
    padding: 1rem;
}


@media screen and (min-width: 2000px) {
    .app__footer p {
        font-size: 19.2px;
    }

    .w__container h4 {
        font-size: 24px;
    }
    .w__container svg {
        font-size: 48px
    }

    .app__footer .social__icons > div {
        width: 48px;
        height: 48px;
    }
    .app__footer .social__icons > div svg {
        font-size: 24px;
    }

    .app__footer ul li {
        font-size: 20.4px;
    }
    .app__footer-links h3 {
        font-size: 24px;
    }

    .app__footer-links_form input {
        font-size: 18px;
    }
}

@media screen and (max-width: 1400px) {
    .app__footer p {
        width: 300px;
    }
}

@media screen and (max-width: 1190px) {
    .app__footer p {
        width: 200px;
        font-size: 13px;
    }

    .w__container h4 {
        font-size: 16.25px;
    }
    .w__container svg {
        font-size: 32.5px
    }

    .app__footer .social__icons > div {
        width: 32.5px;
        height: 32.5px;
    }
    .app__footer .social__icons > div svg {
        font-size: 16.25px;
    }

    .app__footer ul li {
        font-size: 14px;
    }
    .app__footer-links h3 {
        font-size: 16.25px;
    }

    .app__footer-links_form input {
        font-size: 12px;
    }

}

@media screen and (max-width: 960px) {
    .app__footer-links p {
        width: 100%;
    }
    .app__footer-about {
        flex-direction: column;
    }
    .w__container {
        margin-bottom: 1.5rem;
    }
    .app__footer-links {
        flex-direction: column;
        /* justify-content: space-between !important; */
    }
    .app__footer-links_social {
        align-items: center !important;
    }

    .social__icons {
        margin: 0.75rem 0 2rem 0;
    }

    .app__footer-links_links {
        margin: 1rem 0;
    }

    .app__footer-copyright {
        flex-direction: column;
    }
    
}

@media screen and (max-width: 490px) {
    .app__footer-copyright ul {
        flex-direction: column;
    }
    
}