.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    position: absolute;
    font-family: var(--font-primary);
}


.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.app__navbar-logo img {
    width: 150px;
}


.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    /* font-family: var(--font-primary) !important; */
}

.app__navbar-links li {
    margin: 0 1rem;
    cursor: pointer;
}
.app__navbar-links li:hover {
    color: var(--color-grey);
}



.app__navbar-login {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    height: 100%;
}

.app__navbar-login .dash {
    cursor: pointer;
    color: white;
    font-size: 1.5rem;
}

.app__navbar-login .dropdown__menu {
    color: var(--color-black);
    display: none;
    position: absolute;
    right: 0;
    top: 100%;
    background-color: var(--color-white);
    border-radius: 5px;
}
.app__navbar-login:hover .dash {
    color: indianred;
}
.app__navbar-login:hover .dropdown__menu {
    display: block;
}

.app__navbar-login .dropdown__menu ul {
    display: block;
    padding: 0px 0;
}

.app__navbar-login .dropdown__menu ul li {
    width: 160px;
    padding: 10px;
    list-style: none;
    cursor: pointer;
    transition: .3s ease;
}
.app__navbar-login .dropdown__menu ul li:hover {
    color: white;
    background-color: indianred;
    border-radius: 5px;

}

.app__navbar-login .dropdown__menu ul li a {
    padding: 10px 10px;
}


.app__navbar-login .dropdown__menu-small {
    color: var(--color-white);
    display: none;
}

.app__navbar-login .dropdown__menu-small ul {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.app__navbar-login .dropdown__menu-small ul li {
    padding: 10px;
    list-style: none;
    cursor: pointer;
    transition: .3s ease;
}
.app__navbar-login .dropdown__menu-small ul li:hover {
    color: indianred;
}


.app__navbar-smallscreen {
    display: none;
}

.app__navbar-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: var(--color-black);
    transition: .5s ease;
    flex-direction: column;
    z-index: 5;
}

.app__navbar-smallscreen_overlay .overlay__close {
    color: var(--color-golden);
    position: absolute;
    top: 20px;
    right: 20px;
}

.app__navbar-smallscreen_links {
    list-style: none;
}

.app__navbar-smallscreen_links li {
    margin: 2rem;
    cursor: pointer;
    color: var(--color-golden);
    font-size: 2rem;
    text-align: center;
    /* font-family: var(--font-base); */
    /* font-family: var(--font-primary); */
}
.app__navbar-smallscreen_links li:hover {
    color: var(--color-white)
}

@media screen and (min-width: 2000px) {
    .app__navbar-logo img {
        width: 210px;
    }
}

@media screen and (max-width: 1150px) {
    .app__navbar-logo {
        justify-content: flex-start;
    }
    .app__navbar-links {
        display: none;
    }
    .app__navbar-smallscreen {
        display: flex;
    }
    .app__navbar-login .dash {
        display: none;
    }
    .app__navbar-login .dropdown__menu {
        display: none;
    }
    .app__navbar-login .dropdown__menu-small {
        display: block;
        font-size: 14px;
    }
    .app__navbar-login:hover .dropdown__menu {
        display: none;
    }
}

@media screen and (max-width: 650px) {
    .app__navbar {
        padding: 1rem;
    }

    .app__navbar .dropdown__menu-small ul {
        flex-direction: column;
    }
    .app__navbar .dropdown__menu-small ul li:nth-child(2) {
        display: none;
    }

    .app__navbar-logo img {
        width: 110px;
    }
}