.menu-item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.5rem 0;
}

.img-container {
    width: 90px;
    height: 72px;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    margin-right: 1.5rem;
}

.img-container img {
    width: 90px;
    position: absolute;
}

.title-container {
    width: 450px;
}

.menu-title {
    display: flex;
    justify-content: space-between;
}



@media screen and (max-width: 850px) {

    .title-container {
        width: 400px;
    }
}

@media screen and (max-width: 650px) {
    .title-container {
        width: 300px;
    }

}

@media screen and (max-width: 450px) {

    .title-container {
        width: 200px;
    }

}