@font-face {
  font-family: 'Bitter Rose';
  src: url('./assets/Bitter-Rose.ttf');
}
@font-face {
  font-family: 'Chillax';
  src: url('./assets/Chillax-Variable.ttf')
}
@font-face {
  font-family: 'Cormorant Upright';
  src: url('./assets/CormorantUpright-Bold.ttf')
}
@font-face {
  font-family: 'Great Vibes';
  src: url('./assets/GreatVibes-Regular.ttf')
}

:root {
  --font-base: 'Cormorant Upright', serif;
  --font-alt: 'Great Vibes', sans-serif;
  --font-title: 'Montserrat', sans-serif;
  --font-primary: 'Chillax', sans-serif;
  --font-secondary: 'Bitter Rose', sans-serif;

  /* --color-primary: #362417; */
  --color-primary: #FACE8D; /* this is from figma */
  --color-secondary: #92817A;
  --color-dutch: #F1DABF;
  --color-golden: #DCCA87;
  --color-grey: #AAA;
  --color-white: #FFFBFF;
  /* --color-black: #04030F; */
  --color-black: #050505;  /* this is from figma */


  /* --fb-clr: #20415c; */
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

a {
  color: unset;
  text-decoration: none;
}

.slide-bottom {
  -webkit-animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


@-webkit-keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
