.app__reserve .app__reserve-right {
    width: 100%;
    flex-direction: column;
}

.app__reserve .app__reserve-right_p {
    width: 100%;
    padding: 0 3rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}


.app__reserve .app__reserve-right h1 {
    padding: 1rem 1rem;
    font-size: 40px;
    font-weight: 500;
    line-height: 40px;

}
.app__reserve .app__reserve-right p {
    padding: 1rem 1rem;
    font-size: 20px;
    line-height: 32px;
    width: 8    0%;
    opacity: .8;
    text-align: justify;
}

.app__reserve .app__reserve-right form {
    font-size: 16px;
    line-height: 25.6px;
    width: 100%;
}

.app__reserve .app__reserve-right form > div {
    display: flex;
    flex-direction: column;
    margin: 2rem 4rem;
}

.app__reserve .app__reserve-right form > div > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;

}

.app__reserve .app__reserve-right form label {
    opacity: .8;
}

.app__reserve .app__reserve-right form input,
.app__reserve .app__reserve-right form textarea {
    color: var(--color-white);
    padding: .75rem 1rem .75rem 1.5rem;
    border-radius: 10px;
    margin: 1rem 0;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(255, 255, 255, 0.1)
}
.app__reserve .app__reserve-right form input::placeholder,
.app__reserve .app__reserve-right form textarea::placeholder {
    opacity: .8;
    color: var(--color-white);
    font-family: 'Chillax', sans-serif;
}
input[type="date"], input[type="time"] {
    /* color: purple !important; */
    /* background-color: purple !important; */
    position: relative;
    z-index: 2;
}

input[type="date"]::after, input[type="time"]::after {
    content: '';
    position: absolute;
    display: block;
    width: 18%;
    height: 100%;
    background-color: #fff;
    right: 0;
    z-index: -1;
}



@media screen and (min-width: 2000px) {

    .app__reserve .app__reserve-right h1 {
        font-size: 45px;
    }
    .app__reserve .app__reserve-right p {
        font-size: 22.5px;
    }
    

    .app__reserve .app__reserve-right form,
    .app__reserve .app__reserve-right form input,
    .app__reserve .app__reserve-right form textarea {
        font-size: 18px;
    }


}

@media screen and (max-width: 1560px) {

}

@media screen and (max-width: 1200px) {
    .app__reserve {
        flex-direction: column;
        height: 200vh;
    }

}


@media screen and (max-width: 650px) {
    /* .app__reserve .app__reserve-left h3 {
        font-size: 30px;
    }
    .app__reserve .app__reserve-left h2 {
        font-size: 75px;
    }
    
    .app__reserve .app__reserve-left h1 {
        font-size: 60px;
    } */
    .app__reserve .app__reserve-right p {
        font-size: 16px;
    }
    .app__reserve .app__reserve-right form,
    .app__reserve .app__reserve-right form input,
    .app__reserve .app__reserve-right form textarea {
        font-size: 13px;
    }
    .app__reserve .app__reserve-right form input,
    .app__reserve .app__reserve-right form textarea {
        padding-right: 3px;
    }

    input[type="date"]::after, input[type="time"]::after {
        width: 15%;
    }

}

@media screen and (max-width: 450px) {
    /* .app__reserve .app__reserve-left h1 {
        font-size: 50px;
    } */

    .app__reserve .app__reserve-right p {
        font-size: 13px;
    }
    .app__reserve .app__reserve-right form,
    .app__reserve .app__reserve-right form input,
    .app__reserve .app__reserve-right form textarea {
        font-size: 11px;
    }

    .app__reserve .app__reserve-right form input,
    .app__reserve .app__reserve-right form textarea {
        padding-right: 1px;
    }

    input[type="date"]::after, input[type="time"]::after {
        width: 18%;
    }
}