.app__data-left {
    width: 100%;
    height: 100vh;
    /* background: url('../../assets/menu\ left\ bg2.png'); */
    /* background-size: cover;
    background-position: center;
    background-repeat: no-repeat; */
    font-family: 'Chillax', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-between !important;
    align-items: center;
}

.app__data-left h3 {
    margin: 5rem 0 -5rem 0;
    font-size: 32px;
    font-weight: 500;
    line-height: 28.8px;
}
.app__data-left h2 {
    font-family: 'Bitter Rose', sans-serif;
    font-size: 80px;
    font-weight: 400;
    line-height: 72px;
    color: var(--color-primary);
    text-align: center;
}

.app__data-left h1 {
    font-size: 80px;
    font-weight: 500;
    line-height: 80px;
    text-align: center;
}




@media screen and (max-width: 1200px) {
    /* .app__menu {
        flex-direction: column;
        height: 200vh;
    } */

    .app__data-left {
        width: 100vw !important;
        height: 100vh !important;
    }
}

